<template>
  <div class="c-charts" >
    <IEcharts :option="graph"></IEcharts>
  </div>
</template>

<script>
  import IEcharts from 'vue-echarts-v3'
  export default {
    name: "graphChart",
    components:{
      IEcharts
    },
    data: ()=>({
      graph:{
        title: {
          text: 'ECharts 入门示例'
        },
        tooltip: {},
        legend: {
          data:['销量']
        },
        xAxis: {
          data: ["衬衫","羊毛衫","雪纺衫","裤子","高跟鞋","袜子"]
        },
        yAxis: {},
        series: [{
          name: '销量',
          type: 'bar',
          data: [5, 20, 36, 10, 10, 20]
        }]
      }
    })
  }
</script>

<style scoped>
  .el-col{
    margin-bottom:16px;
  }
  .material-icons{
    font-size:80px;
    color:#ddd;
  }

  .cart-string{
    height:100px;
    padding-top:10px;
    font-size:1.1rem;

  }
  .c-charts{
    height:400px;
    width:100%;
  }
</style>



