<template>
  <div>
    <v-PageTitle vTitle="模型展示"></v-PageTitle>
    <div class="model">模型</div>
  </div>
</template>

<script>
  import vPageTitle from '../common/pageTitle'
  import {quillEditor} from 'vue-quill-editor'
    export default {
      name: "EditorPage",
      components:{
        vPageTitle,
        quillEditor
      },
      data(){
        return{
          content:'<h2>JSPangAdmin 是一个由Vue2为框架开发的后台管理系统。</h2><h3>你可以随意的使用并编辑它，希望可以帮助前端开发者减轻开发步骤，让大家有时间泡妞和享受生活。</h3>',
          editorOption: {}
        }
      },
      methods: {
        onEditorBlur(editor) {
          console.log('editor blur!', editor)
        },
        onEditorFocus(editor) {
          console.log('editor focus!', editor)
        },
        onEditorReady(editor) {
          console.log('editor ready!', editor)
        }
      },
      computed: {
        editor() {
          return this.$refs.myTextEditor.quillEditor;
        }
      }
    }
</script>

<style scoped>
  .el-col{
    margin-bottom:16px;
  }
  .material-icons{
    font-size:80px;
    color:#ddd;
  }

  .ql-container .ql-editor {
    min-height: 20em;
    padding-bottom: 1em;
    max-height: 25em;

  }
  .html {
    height: 9em;
    overflow-y: auto;
    border: 1px solid #ccc;
    border-top: none;
    resize: vertical;
    background-color:#fff;
  }
  .model{
    width: 1600px;
    height: 800px;
    line-height: 800px;
    border: 3px solid #3E3E3E;
    color: #0a76a4;
    font-size: 55px;
    text-align: center;
  }
</style>



