<template>
  <div>
    <v-PageTitle vTitle="日程日志"></v-PageTitle>
    <el-row>
      <el-col :span="24">
        <el-card class="box-card">
          InlineForm-行布局
          <hr>
          <inlineForm></inlineForm>
        </el-card>
      </el-col>
    </el-row>

    <el-row :gutter="20">
      <el-col :xs="24" :sm="24" :md="8" :lg="8">
        <el-card class="box-card card-a">
          Right Layout Form
          <hr>
          <rightLayoutForm></rightLayoutForm>
        </el-card>
      </el-col>
      <el-col :xs="24" :sm="24" :md="8" :lg="8">
        <el-card class="box-card card-a">
          Left Layout Form
          <hr>
          <leftLayoutForm></leftLayoutForm>
        </el-card>
      </el-col>
      <el-col :xs="24" :sm="24" :md="8" :lg="8">
        <el-card class="box-card card-a">
          Top Layout Form
          <hr>
          <topLayoutForm></topLayoutForm>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import vPageTitle from '../common/pageTitle'
  import inlineForm from '../forms/inlineForm'
  import rightLayoutForm from '../forms/rightLayoutForm'
  import leftLayoutForm from '../forms/leftLayoutForm'
  import topLayoutForm from '../forms/topLayoutForm'
  export default {
    name: "FormInput",
    components:{
      vPageTitle,
      inlineForm,
      rightLayoutForm,
      leftLayoutForm,
      topLayoutForm
    }
  }
</script>

<style scoped>
  .el-col{
    margin-bottom:16px;
  }
  .box-card{
    font-size:12px;
  }
  .box-card hr{
    height:1px;
    border:none;
    border-top:1px  dashed #ccc;
    margin-bottom:10px;
    margin-top:6px;
  }
  .card-a{
    height:300px;
  }
</style>



