    user-select: none;
  }
  .sign ul li {
    position: relative;
    float: left;
    width: 15%;
    margin-right: 2%;
    margin-left: 0;
    display: table;
  }
  .am-dropdown {
    background-color: rgba(108, 168, 255, 0.2);
    color: white;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
  }
  .icon1{
    font-size: 14px;
    color: #fff;
    margin-left: -5px;
    margin-top: 2px;
  }
  .am-selected-btn {
    font-size: 8px;
    color: white;
    background-color: rgba(108, 168, 255, 0.2);
    border-color: rgba(108, 168, 255, 0.2);
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
  }

  .am-selected-content {
    background-color: rgba(108, 168, 255, 0.2);
    overflow-x: auto;
    overflow-y: hidden;
    font-size: 8px;
  }

  .am-selected-list::-webkit-scrollbar {
    font-size: 8px;
    display: none;
  }

  .am-selected-list li {
    font-size: 8px;
  }

  .am-selected-list li:hover {
    font-size: 8px;
    width: 100%;
    background-color: rgba(248, 248, 248, 0.2);
  }

  .am-btn-default:hover,
  .am-btn-default:focus {
    font-size: 8px;
    color: white;
  }

  .am-dropdown.am-active .am-btn-default.am-dropdown-toggle {
    background-color: rgba(108, 168, 255, 0.2);
    color: white;
    font-size: 8px;
  }

  .header {
    width: 100%;
    position: relative;
    background: url(img/header.png) no-repeat center top;
    background-size: 100%;
    height: 84px;
  }

  .header h1 {
    text-align: center;
    line-height: 60px;
    height: 60px;
    color: white;
  }

  .text-span {
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    float: left;
    background-color: rgba(108, 168, 255, 0.2);
    color: white;
    width: 60px;
    border: 1px solid;
    border-color: rgba(108, 168, 255, 0.2);
    display: inline-block;
    height: 29px;
    line-height: 29px;
    text-align: center;
    font-size: 10px;
  }

  .am-btn-sm {
    font-size: 1rem;
  }

  #modelBox {
    width: 90%;
    border-radius: 10px;
    background: rgba(24, 36, 54, 1);
  }

  .weatherS {
    float: right;
    margin-top: 10px;
    margin-right: 10px;
  }

  .weatherS img {
    display: inline-block;
    height: 14px;
    width: 15px;
    margin-right: 5px;
  }

  .weatherS span {
    font-size: 14px;
    color: white;
  }

  .sign {
    width: 90%;
    margin-left: 5%;
    position: relative;
    bottom: 8%;
  }

  .sign ul {
    width: 100%;
    margin-left: 0;
    position: relative;
  }

  .sign ul li {
    position: relative;
    float: left;
    width: 15%;
    margin-right: 2%;
    margin-left: 0;
    display: table;
  }

  .sign ul li a {
    display: table-cell;
    vertical-align: middle;
    margin-left: 20%;
    margin-right: 20%;
    background-image: url(img/button.png);
    background-size: 100%;
    text-align: center;
    font-size: 10px;
    color: white;
    cursor: pointer;
  }

  .sign ul li a span {
    margin-left: 20%;
    display: block;
    width: 60%;
  }

  .stick {
    width: 30%;
    position: absolute;
    left: 35%;
    display: none;
  }

  .stickBox {
    z-index: 10;
    width: 200%;
    font-size: 8px;
    color: white;
    position: absolute;
    left: -50%;
    padding: 10px;
    background: rgba(24, 36, 54, .86);
    border: solid 1px rgba(0, 198, 251, .6);
    display: none;
  }

  .sign ul li:hover .stick {
    display: block;
  }

  .sign ul li:hover .stickBox {
    display: block;
  }

  .stickBox img {
    position: absolute;
    width: 10px;
    height: 10px;
  ;
    z-index: 11;
  }

  .lt {
    top: -2px;
    left: -2px;
    transform: rotate(270deg);
    -ms-transform: rotate(270deg);
    /* IE 9 */
    -moz-transform: rotate(270deg);
    /* Firefox */
    -webkit-transform: rotate(270deg);
    /* Safari 和 Chrome */
    -o-transform: rotate(270deg);
  }

  .rt {
    top: -2px;
    right: -2px;
  }

  .rb {
    bottom: -2px;
    right: -2px;
    transform: rotate(90deg);
    -ms-transform: rotate(90deg);
    /* IE 9 */
    -moz-transform: rotate(90deg);
    /* Firefox */
    -webkit-transform: rotate(90deg);
    /* Safari 和 Chrome */
    -o-transform: rotate(90deg);
  }

  .lb {
    bottom: -2px;
    left: -2px;
    transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    /* IE 9 */
    -moz-transform: rotate(180deg);
    /* Firefox */
    -webkit-transform: rotate(180deg);
    /* Safari 和 Chrome */
    -o-transform: rotate(180deg);
  }
  .am-selected {
    width: 200px;
  }
  .am-selected-btn {
    width: 100%;
    padding-left: 10px;
    text-align: right;
  }
  .am-selected-btn.am-btn-default {
    background: none;
  }
  .am-invalid .am-selected-btn {
    border-color: #dd514c;
  }
  .am-selected-header {
    height: 45px;
    background-color: #f2f2f2;
    border-bottom: 1px solid #ddd;
    display: none;
  }
  .am-selected-status {
    text-align: left;
    width: 100%;
    display: block;
    word-wrap: normal;
    /* for IE */
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }
  .am-selected-content {
    padding: 10px 0;
  }
  .am-selected-search {
    padding: 0 10px 10px;
  }
  .am-selected-search .am-form-field {
    padding: .5em;
  }
  .am-selected-list {
    margin: 0;
    padding: 0;
    list-style: none;
    font-size: 1.5rem;
  }
  .am-selected-list li {
    position: relative;
    cursor: pointer;
    padding: 5px 10px;
    -webkit-transition: background-color 0.15s;
    transition: background-color 0.15s;
  }
  .am-selected-list li:hover {
    background-color: #f8f8f8;
  }
  .am-selected-list li:hover .am-icon-check {
    opacity: .6;
  }
  .am-selected-list li.am-checked .am-icon-check {
    opacity: 1;
    color: #0e90d2;
  }
  .am-selected-list li.am-disabled {
    opacity: .5;
    pointer-events: none;
    cursor: not-allowed;
  }
  .am-selected-list .am-selected-list-header {
    margin-top: 8px;
    font-size: 1.3rem;
    color: #999999;
    border-bottom: 1px solid #e5e5e5;
    cursor: default;
  }
  .am-selected-list .am-selected-list-header:hover {
    background: none;
  }
  .am-selected-list .am-selected-list-header:first-child {
    margin-top: 0;
  }
  .am-selected-list .am-selected-text {
    display: block;
    word-wrap: normal;
    /* for IE */
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    margin-right: 30px;
  }
  .am-selected-list .am-icon-check {
    position: absolute;
    right: 8px;
    top: 5px;
    color: #999999;
    opacity: 0;
    -webkit-transition: opacity .15s;
    transition: opacity .15s;
  }
  .am-selected-hint {
    line-height: 1.2;
    color: #dd514c;
  }
  .am-selected-hint:not(:empty) {
    margin-top: 10px;
    border-top: 1px solid #e5e5e5;
    padding: 10px 10px 0;
  }
</style>



