<template>
  <div class="pageTitle">
    <div class="pt-left">
      <h2>{{vTitle.toUpperCase()}}</h2>
    </div>
    <div class="pt-right">
      <span class="pt-Home">Home</span>
      <span class="pt-line"> / </span>
      <span class="pt-page">{{vTitle}}</span>
    </div>
  </div>
</template>

<script>
    export default {
      name: "pageTitle",
      props:['vTitle']
    }
</script>

<style scoped>
  .pageTitle{
    clear:both;
    height:55px;
  }
  .pt-left{
    float:left;
  }
  .pt-right{
    float:right;
  }
  .pt-Home{
    font-size:20px;
    color: #209e91;
  }
  .pt-line{
    color:#ccc;
  }
  a{
    color:#209e91;
  }
</style>



