<template>
  <div>
    <v-PageTitle vTitle="质量统计"></v-PageTitle>
    <el-row :gutter="20">
      <el-col :xs="24" :sm="24" :md="12" :lg="12">
        <el-card class="box-card">
          HOVER ROWS
          <hr>
          <hoverRow></hoverRow>
        </el-card>
      </el-col>
      <el-col :xs="24" :sm="24" :md="12" :lg="12">
        <el-card class="box-card">
          STRIPED  ROWS
          <hr>
          <hoverRow></hoverRow>
        </el-card>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :xs="24" :sm="24" :md="12" :lg="12">
        <el-card class="box-card">
          BORDERED TABLE
          <hr>
          <borderTable></borderTable>
        </el-card>
      </el-col>
      <el-col :xs="24" :sm="24" :md="12" :lg="12">
        <el-card class="box-card">
          CONDENSED TABLE
          <hr>
          <condensedTable></condensedTable>
        </el-card>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :xs="24" :sm="24" :md="12" :lg="12">
        <el-card class="box-card">
          FIXED HEADER TABLE
          <hr>
          <fixedHeaderTable></fixedHeaderTable>
        </el-card>
      </el-col>
      <el-col :xs="24" :sm="24" :md="12" :lg="12">
        <el-card class="box-card">
          FIXED COLUMN TABLE
          <hr>
          <fixedColumnTable></fixedColumnTable>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import vPageTitle from '../common/pageTitle'
  import hoverRow from '../tables/hoverRow'
  import borderTable from '../tables/borderTable'
  import condensedTable from '../tables/condensedTable'
  import fixedHeaderTable from '../tables/fixedHeaderTable'
  import fixedColumnTable from '../tables/fixedColumnTable'
    export default {
      name: "BasicTables",
      components:{
        vPageTitle,
        hoverRow,
        borderTable,
        condensedTable,
        fixedHeaderTable,
        fixedColumnTable
      }
    }
</script>

<style scoped>
  .el-col{
    margin-bottom:16px;
  }
  .box-card{
    font-size:12px;
  }
  .box-card hr{
    height:1px;
    border:none;
    border-top:1px  dashed #ccc;
    margin-bottom:10px;
    margin-top:6px;
  }
</style>



