<template>
  <div>
    <v-PageTitle vTitle="用户及权限"></v-PageTitle>
    <template>
      <el-table
        :data="tableData"
        border
        style="width: 100%">
        <el-table-column
          fixed
          prop="date"
          label="姓名"
          width="350">
        </el-table-column>
        <el-table-column
          prop="name"
          label="用户名"
          width="320">
        </el-table-column>
        <el-table-column
          prop="province"
          label="公司名称"
          width="320">
        </el-table-column>
        <el-table-column
          prop="city"
          label="电话"
          width="320">
        </el-table-column>
        <el-table-column
          prop="address"
          label="邮箱"
          width="300">
        </el-table-column>
        <el-table-column
          prop="address"
          label="邮箱"
          width="300">
        </el-table-column>
        <el-table-column
          prop="address"
          label="邮箱"
          width="300">
        </el-table-column>
        <el-table-column
          fixed="right"
          label="操作"
          width="100">
          <template slot-scope="scope">
            <el-button type="text" @click="open3">添加</el-button>
            <el-button  @click.native.prevent="deleteRow(scope.$index, tableData)" type="text" size="small">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </template>
  </div>
</template>

<script>
  import vPageTitle from '../common/pageTitle'
  export default {
    name: "tablePage",
    data() {
      return {
        tableData: [{
          date: '2016-05-03',
          name: '王小虎',
          province: '上海',
          city: '普陀区',
          address: '455161611@qq.com',
          zip: 200333
        }, {
          date: '2016-05-02',
          name: '王小虎',
          province: '上海',
          city: '普陀区',
          address: '455161611@qq.com',
          zip: 200333
        }, {
          date: '2016-05-04',
          name: '王小虎',
          province: '上海',
          city: '普陀区',
          address: '455161611@qq.com',
          zip: 200333
        }, {
          date: '2016-05-01',
          name: '王小虎',
          province: '上海',
          city: '普陀区',
          address: '455161611@qq.com',
          zip: 200333
        }]
      }
    },
    methods: {
      deleteRow(index, rows) {
        rows.splice(index, 1);
      },
      open3() {
        this.$prompt('添加用户', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
          inputErrorMessage: '请输入用户信息'
        }).then(({ value }) => {
          this.$message({
            type: 'success',
            message: '你的邮箱是: ' + value
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '取消输入'
          });
        });
      }
    },
    components:{
      vPageTitle
    }
  }
</script>

<style scoped>

</style>



