<template>
  <div>
    <input type="text" v-model="inputContent" placeholder="Take to do" class="my-input" v-on:keyup.enter="onEnter()">
    <ul class="todolist" style="padding: 0">
      <li v-for="list in listContents">
        {{list.content}}
      </li>
    </ul>
  </div>
</template>

<script>
    import vPageTitle from '../common/pageTitle'
    export default {
      name: "todoList",
      components:{
        vPageTitle
      },
      data(){
        return{
          inputContent:'',
          listContents:[
            {content:'What do you think?',isDone:false},
            {content:'Have fun with blue admin',isDone:false},
            {content:'Write email to business cat',isDone:false},
            {content:'Get in touch with jspang team',isDone:false},
            {content:'Ei cum soleet appareat,ex est graeci medioc',isDone:false},
            {content:'Simaul erroribus ad usu',isDone:false},
            {content:'Vim an eius ocurreret abhorreant,id name a',isDone:false}
          ]
        }
      },
      methods:{
        onEnter:function () {
          if (this.inputContent != ''){
            var listContents = {content:this.inputContent,isDone:true};
            this.listContents.splice(0,0,listContents);
            this.inputContent = '';
          }
        }
      }
    }
</script>

<style scoped>
  .el-col{
    margin-bottom:16px;
  }
  .todolist{
    margin-top:5px;
    list-style:none;
    border:1px solid #ccc;
    border-radius:6px;
  }
  .todolist li{
    transition:all 0.5s;
    -moz-transition:all 0.5s;
    -o-transition:all 0.5s;
    -webkit-transition:all 0.5s;
    line-height:20px;
    padding:10px;
    border-bottom:1px solid #ccc;
    cursor:pointer;
    border-left:5px solid #209e91;
    text-align:left;
  }
  .todolist li:nth-child(2n){
    border-left:5px solid #0e8174;
  }

  .todolist li:hover{
    border-left:30px solid #209e91;
    background-color:#eee;
  }
  .my-input{
    line-height:28px;
    border:1px solid #ccc;
    border-radius:6px;
    width:98%;
    padding:5px;
  }
</style>



