<template>
  <div >
    <v-PageTitle vTitle="组件"></v-PageTitle>
    <el-row :gutter="20">
      <el-col :xs="24" :sm="24" :md="8" :lg="8">
        <el-card class="box-card">
          <barChart></barChart>
        </el-card>
      </el-col>
      <el-col :xs="24" :sm="24" :md="8" :lg="8">
        <el-card class="box-card">
          <lineChart></lineChart>
        </el-card>
      </el-col>
      <el-col :xs="24" :sm="24" :md="8" :lg="8">
        <el-card class="box-card">
          <radarChart></radarChart>
        </el-card>
      </el-col>
    </el-row>

    <el-row :gutter="20">
      <el-col :xs="24" :sm="24" :md="12" :lg="12">
        <el-card class="box-card">
          <pieChart></pieChart>
        </el-card>
      </el-col>
      <el-col :xs="24" :sm="24" :md="12" :lg="12">
        <el-card class="box-card">
          <funnelChart></funnelChart>
        </el-card>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :xs="24" :sm="24" :md="24" :lg="24">
        <el-card class="box-card">
          <el-carousel :interval="2000" arrow="always">
            <el-carousel-item v-for="item in 4" :key="item">
              <h3>{{ item }}</h3>
            </el-carousel-item>
          </el-carousel>
        </el-card>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :xs="24" :sm="24" :md="24" :lg="24">
        <el-card class="box-card">
          <sunlightChart></sunlightChart>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import vPageTitle from '../common/pageTitle.vue';
  import barChart from '../charts/barChart.vue';
  import pieChart from '../charts/pieCharts.vue';
  import lineChart from '../charts/lineChart.vue';
  import radarChart from '../charts/radarChart.vue';
  import funnelChart from '../charts/funnelChart.vue';
  import sunlightChart from '../charts/sunlightChart.vue';

  export default {
    components:{
      vPageTitle,
      barChart,
      pieChart,
      lineChart,
      radarChart,
      funnelChart,
      sunlightChart
    },
    methods: {
    }
  }
</script>

<style scoped>
  .el-col{
    margin-bottom:16px;
  }
  .material-icons{
    font-size:80px;
    color:#ddd;
  }

  .cart-string{
    height:100px;
    padding-top:10px;
    font-size:1.1rem;

  }
  .el-carousel__item h3 {
    color: #475669;
    font-size: 55px;
    opacity: 0.75;
    text-align: center;
    line-height: 300px;
    background-color: pink;
    margin: 0;
    color: red;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }

  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }


</style>



