<template>
  <div >
    <v-PageTitle vTitle="BasicCharts"></v-PageTitle>
    <el-row :gutter="20">
      <el-col :xs="24" :sm="24" :md="12" :lg="12">
        <el-card class="box-card">
          <qualityChart></qualityChart>
        </el-card>
      </el-col>
      <el-col :xs="24" :sm="24" :md="12" :lg="12">
        <el-card class="box-card">
          <graphChart></graphChart>
        </el-card>
      </el-col>
    </el-row>
    <el-row :gutter="10">
      <el-col :xs="24" :sm="24" :md="24" :lg="24">
        <el-card class="box-card">
          <modelChart></modelChart>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import vPageTitle from '../common/pageTitle.vue';
  import barChart from '../charts/barChart.vue';
  import pieChart from '../charts/pieCharts.vue';
  import lineChart from '../charts/lineChart.vue';
  import radarChart from '../charts/radarChart.vue';
  import funnelChart from '../charts/funnelChart.vue';
  import qualityChart from '../charts/qualityChart.vue';
  import graphChart from '../charts/graphChart.vue';
  import modelChart from '../charts/modelChart.vue';
    export default {
      name: "massDistribution",
      components:{
        vPageTitle,
        barChart,
        pieChart,
        lineChart,
        radarChart,
        funnelChart,
        qualityChart,
        graphChart,
        modelChart
      }
    }
</script>

<style scoped>
  .el-col{
    margin-bottom:16px;
  }
  .material-icons{
    font-size:80px;
    color:#ddd;
  }

  .cart-string{
    height:100px;
    padding-top:10px;
    font-size:1.1rem;

  }
</style>



