import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/common/Home.vue'
import BashBoard from '@/components/page/BashBoard.vue'
import EditorPage from '@/components/page/EditorPage.vue'
import TablePage from '@/components/page/tablePage.vue'
import markDownPage from '@/components/page/markDownPage.vue'
import todoListPage from '@/components/page/todoListPage.vue'
import BasicCharts from '@/components/page/BasicCharts.vue'
import FormInput from '@/components/page/FormInput.vue'
import FormLayouts from '@/components/page/FormLayouts.vue'
import BasicTables from '@/components/page/BasicTables.vue'
import EngineeringPage from '@/components/page/EngineeringPage.vue'
import MassDistribution from '@/components/page/massDistribution.vue'
import dangerPage from '@/components/page/dangerPage.vue'
import dangerPage1 from '@/components/page/dangerPage1.vue'
import dangerPage2 from '@/components/page/dangerPage2.vue'
import dangerPage3 from '@/components/page/dangerPage3.vue'
import dangerPage4 from '@/components/page/dangerPage4.vue'
import dangerPage5 from '@/components/page/dangerPage5.vue'
import dangerPage6 from '@/components/page/dangerPage6.vue'
import contractPage from '@/components/page/contractPage.vue'
import designPage from '@/components/page/designPage.vue'
import userPage from '@/components/page/userPage.vue'
import pagingPage from '@/components/page/pagingPage.vue'
import administrationPage from '@/components/page/administrationPage.vue'
import organizationPage from '@/components/page/organizationPage.vue'
import dataPage from '@/components/page/dataPage.vue'
import monitorPage from '@/components/page/monitorPage.vue'


Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      component: Home,
      children:[
        {
          path: '',
          component: BashBoard,
        },
        {
          path: '/BashBoard',
          component: BashBoard,
        },
        {
          path:'/EditorPage',
          component:EditorPage
        },
        {
          path:'/TablePage',
          component:TablePage
        },
        {
          path:'/markDownPage',
          component:markDownPage
        },
        {
          path:'/todoListPage',
          component:todoListPage
        },
        {
          path:'/BasicCharts',
          component:BasicCharts
        },
        {
          path:'/FormInput',
          component:FormInput
        },
        {
          path:'/FormLayouts',
          component:FormLayouts
        },
        {
          path:'/BasicTables',
          component:BasicTables
        },
        {
          path:'/EngineeringPage',
          component:EngineeringPage
        },
        {
          path:'/MassDistribution',
          component:MassDistribution
        },
        {
          path:'/dangerPage',
          component:dangerPage
        },
        {
          path:'/dangerPage1',
          component:dangerPage1
        },
        {
          path:'/dangerPage2',
          component:dangerPage2
        },
        {
          path:'/dangerPage3',
          component:dangerPage3
        },
        {
          path:'/dangerPage4',
          component:dangerPage4
        },
        {
          path:'/dangerPage5',
          component:dangerPage5
        },
        {
          path:'/dangerPage6',
          component:dangerPage6
        },
        {
          path:'/contractPage',
          component:contractPage
        },
        {
          path:'/designPage',
          component:designPage
        },
        {
          path:'/userPage',
          component:userPage
        },
        {
          path:'/pagingPage',
          component:pagingPage
        },
        {
          path:'/administrationPage',
          component:administrationPage
        },
        {
          path:'/organizationPage',
          component:organizationPage
        },
        {
          path:'/dataPage',
          component:dataPage
        },
        {
          path:'/monitorPage',
          component:monitorPage
        }
      ]
    }
  ]
})



