<template>
  <div class="c-charts" >
    <IEcharts :option="quality"></IEcharts>
  </div>
</template>

<script>
  import IEcharts from 'vue-echarts-v3'
    export default {
      name: "qualityChart",
      components:{
        IEcharts
      },
      data: ()=>({
        quality:{
          title : {
            text: '某站点用户访问来源',
            subtext: '纯属虚构',
            x:'center'
          },
          tooltip : {
            trigger: 'item',
            formatter: "{a} <br/>{b} : {c} ({d}%)"
          },
          legend: {
            orient: 'vertical',
            left: 'left',
            data: ['直接访问','邮件营销','联盟广告','视频广告','搜索引擎']
          },
          series : [
            {
              name: '访问来源',
              type: 'pie',
              radius : '55%',
              center: ['50%', '60%'],
              data:[
                {value:335, name:'直接访问'},
                {value:310, name:'邮件营销'},
                {value:234, name:'联盟广告'},
                {value:135, name:'视频广告'},
                {value:1548, name:'搜索引擎'}
              ],
              itemStyle: {
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
              }
            }
          ]
        }
      })
    }
</script>

<style scoped>
  .el-col{
    margin-bottom:16px;
  }
  .material-icons{
    font-size:80px;
    color:#ddd;
  }

  .cart-string{
    height:100px;
    padding-top:10px;
    font-size:1.1rem;

  }
  .c-charts{
    height:400px;
    width:100%;
  }
</style>



